<template>
  <div class="card">
    <h4 class="title overflow" :title="track.name">{{ track.name }}</h4>
    <Picture :images="track.album.images" :name="track.name + ' track picture'"/>
    <h5 class="information overflow" :title="track.artists[0].name">{{track.artists[0].name}}</h5>
    <h5 class="information overflow" :title="'Runtime:' + formatRuntime()">Runtime: {{formatRuntime()}}</h5>
  </div>
</template>

<script>
import Picture from './Picture'

export default {
  name: 'Track',
  components: { Picture },
  props: {
    track: Object
  },
  methods: {
    formatRuntime () {
      return new Date(this.track.duration_ms).toISOString().slice(11, -5)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../assets/styles/card.scss";
</style>
