<template>
  <div>
    <div class="results" v-if="hasResults()">
      <Artists v-if="results['artists']" v-bind:artists="results['artists'].items"/>
      <Albums v-if="results['albums']" v-bind:albums="results['albums'].items"/>
      <Tracks v-if="results['tracks']" v-bind:tracks="results['tracks'].items"/>
    </div>
    <div class="no-results" v-if="!hasResults()">
      <h2>No results were found.</h2>
    </div>
  </div>
</template>

<script>
import Albums from './Albums.vue'
import Artists from './Artists.vue'
import Tracks from './Tracks.vue'

export default {
  components: { Albums, Artists, Tracks },
  name: 'Results',
  props: {
    results: Object
  },
  methods: {
    hasResults () {
      return this.results.albums.total + this.results.artists.total + this.results.tracks.total !== 0
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../assets/styles/theme.scss";

  .results {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }

  .no-results {
    height: 100vh;
    @media screen and (min-width: $small-screen) {
      margin-left: 5rem;
      margin-right: 5rem;
    }
    background: $special-white;
  }
</style>
